import React from 'react';

class App extends React.Component {

    constructor(props) {
        super (props);

        this.state = {
            count: 0
        };
        this.handleClick = this.handleClick.bind(this);
    }

    render () {
        return (
            <div>
                <button onClick={this.handleClick}>Click {`${this.state.count}`}</button>
            </div>
        );
    }

    handleClick = (e) => {
        let {count} = this.state;

        this.setState ({
            count: count+1
        });
    }
}

export default App;